@font-face {
  font-family: "jmh";
  src: url("../src/fonts/jmh_psychedelic_caps-webfont.woff") format("woff"), url("../src/fonts/jmh_psychedelic_caps-webfont.woff2") format("woff2");
}

:root {
  --mainbg: url("../src/images/bg2.png");
  --gradient1: linear-gradient(
    to bottom right,
    rgba(255, 65, 108, 0.75) 0%,
    rgba(255, 75, 43, 0.75) 100%
  );
  --gradient2: linear-gradient(
    to bottom right,
    rgba(195, 20, 50, 0.75) 0%,
    rgba(36, 11, 54, 0.75) 100%
  );
  --gradient3: linear-gradient(
    to bottom right,
    rgba(255, 65, 108, 0.25) 0%,
    rgba(255, 75, 43, 0.25) 100%
  );
  --font1: "jmh";
  --font2: "Nova Mono", monospace;
  --font3: "Quicksand", sans-serif;
  --unit: 8px;
  --mclr: 195, 20, 50;
  --hclr: 207, 139, 243;
  --gclr: 253, 185, 155;
  --c1: 167, 112, 239;
  --c2: 207, 139, 243;
  --c3: 253, 185, 155;
  --hx1: #ff416c;
  --hx2: #c31432;
  --hx3: #240b36;
  --x4: #40e0d0;
  --x5: #ff8c00;
  --x6: #ff0080;
}

.toplinks-thrive {
  width: 1200px;
  margin-left: -75px;
  margin-right: -75px;
  font-family: var(--font2);
  font-size: 10px;
  padding-left: 10px;
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-bottom: 1rem;
}

.toplinks-thrive a:after {
  content: "/";
  display: inline-block;
  margin: 0 2rem;
}

.toplinks-thrive a:last-of-type:after {
  display: none;
}

.thrivin-stats {
  width: 1200px;
  margin-left: -75px;
  margin-right: -75px;
  margin-bottom: -75px;
  background: #fff;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  font-size: calc(var(--unit) * 2);
}

.thrivin-stats .topside {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 75px;
  border-bottom: 1px solid #ddd;
}

.thrivin-stats .topside .currently-online {
  width: 450px;
}

.thrivin-stats .topside .currently-online .online-count {
  font-family: var(--font3);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 25px;
  border-bottom: 1px solid #ddd;
  padding-bottom: 1rem;
}

.thrivin-stats .topside .currently-online .online-numbers {
  font-family: var(--font2);
  font-size: 10px;
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-top: 1rem;
}

.thrivin-stats .topside .currently-online .online-numbers span {
  color: rgba(var(--c1), 1);
}

.thrivin-stats .topside .currently-online .online-list {
  margin-top: 2rem;
  height: 200px;
  overflow: auto;
}

.thrivin-stats .topside .currently-online .online-list a {
  font-family: var(--font1);
  font-size: 18px;
  text-transform: lowercase;
  letter-spacing: 1px;
}

.thrivin-stats .topside .currently-online .online-list a span {
  background: linear-gradient(-90deg, rgba(var(--mclr), 1) 0%, rgba(var(--hclr), 1) 50%, rgba(var(--gclr), 1) 100%);
  display: inline-block;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}

.thrivin-stats .topside .misc-stats {
  width: 250px;
}

.thrivin-stats .topside .misc-stats .stat-bubble {
  text-align: center;
  font-family: var(--font2);
  font-size: 10px;
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-bottom: 6rem;
}

.thrivin-stats .topside .misc-stats .stat-bubble:last-of-type {
  margin-bottom: 0;
}

.thrivin-stats .topside .misc-stats .stat-bubble span {
  font-family: var(--font3);
  margin-bottom: 1rem;
  font-size: 24px;
  display: inline-block;
  background: linear-gradient(to right, rgba(var(--c1), 1), rgba(var(--c2), 1), rgba(var(--c3), 1)) no-repeat bottom left/100% 1px;
}

.thrivin-stats .topside .fix-recent-topics {
  width: 350px;
  height: 376px;
  overflow: auto;
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}

.thrivin-stats .topside .fix-recent-topics li.rt-item {
  width: 100%;
  background: var(--maingrad);
  padding-left: 6px;
  margin-bottom: 1rem;
}

.thrivin-stats .topside .fix-recent-topics li.rt-item .rt-wrap {
  background: #111;
  padding: 24px;
  font-family: var(--font2);
  color: #fff;
  font-size: 9px;
  text-transform: uppercase;
}

.thrivin-stats .topside .fix-recent-topics li.rt-item .rt-wrap a.rt-name {
  display: block;
  font-family: var(--font4);
  text-transform: lowercase;
  letter-spacing: 0;
  font-size: 16px;
  color: #fff;
  margin-bottom: 0.5rem;
}

.thrivin-stats .topside .fix-recent-topics li.rt-item .rt-wrap a.rt-user {
  color: rgba(var(--c3), 1);
  background: #fff;
  padding: 2px 4px 1px 4px;
  border-radius: 3px;
}

.thrivin-stats .bottomside {
  background: #111;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-between;
  padding: 25px;
}

.thrivin-stats .bottomside .recent-online {
  width: 32%;
  font-family: var(--font2);
  font-size: 8px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #fff;
  max-height: 25px;
  overflow: auto;
}

.thrivin-stats .bottomside .recent-online a span {
  color: var(--x1);
}

.thrivin-stats .bottomside .birthdays,
.thrivin-stats .bottomside .welcome {
  text-align: center;
  width: 32%;
  font-family: var(--font2);
  font-size: 8px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #fff;
}

.thrivin-stats .bottomside .welcome {
  text-align: right;
}

.thrivin-stats .bottomside .welcome a {
  color: rgba(var(--c2), 1);
}